import React from "react";
import styled, { css } from "styled-components";

import { FlexBoxContentCenter } from "../FlexBox";

const StyledHeaderTitle = styled.div`
  ${({ theme }) => {
    const { color } = theme;
    return css`
      width: 169px;
      height: 64px;
      background-color: ${color.ultramarineBlue};
      font-size: 24px;
      font-weight: 700;
    `;
  }}
`;

export default function HeaderTitle(): JSX.Element {
  return <StyledHeaderTitle>R.js</StyledHeaderTitle>;
}
